body {
    margin : 0;
    overflow-y : hidden;
}

.outer {
    height : 100vh;
    overflow-y: auto;
}

.outer::-webkit-scrollbar {
    display: none;
}

.inner{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 100px;
}

.bg-1 {
    background-color: white;
}

.bg-2 {
    background-color: #61dafb;
}

.bg-3 {
    background-color: lightseagreen;
}